<template>
  <div class="footerOuter">
    <div class="footerInner">Copyright © 2022 Mr.AQ All Rights Reserved.</div>
    <div class="pageBtn" @click="scrollTop" :class="{ showPageTop }">
      <i class="fas fa-chevron-up pageBtnIcon"></i>
    </div>
  </div>
</template>
<style scoped>
@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.10.2/css/all.min.css";
.footerOuter {
  display: table;
  height: 100px;
  width: 100%;
  text-align: center;
  z-index: 2;
  border-style: solid none none none;
  border-top-width: 1px;
  background-color: rgb(252, 225, 230);
}

.footerInner {
  display: table-cell;
  vertical-align: middle;
  height: 50px;
}

/* footer*/
.pageBtn {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 48px;
  height: 48px;
  line-height: 32px;
  text-align: center;
  border-radius: 50%;
  background: #5bc8ac;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
}
.showPageTop {
  /* .boxの状態変化後を指定 */
  opacity: 1;
}

.pageBtnIcon {
  color: #fff;
  margin-top: 5px;
  font-size: 32px;
}
</style>
<script>
import { ref, onMounted } from "vue";
export default {
  methods: {
    scrollTop: function () {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
  setup() {
    const showPageTop = ref(true);
    let onScroll = function () {
      if (window.scrollY > 200) {
        showPageTop.value = true;
      } else {
        showPageTop.value = false;
      }
    };
    onMounted(() => {
      window.addEventListener("scroll", onScroll);
    });
    return {
      showPageTop,
    };
  },
};
</script>
