import { defineStore } from "pinia";
import { User, RollList } from "types/global";
import { ROLL_LIST } from "@/const/roll";

export const useStore = defineStore("test", {
  state: () => ({
    user: {
      nameMaster: "ご主人様",
      nameSub: "さき",
    } as User,
    rolls: [] as RollList[],
  }),

  // getters -> 状態を取得するgetter（computedに相当）
  getters: {
    rollList: (state) => {
      if (state.rolls.length == 0) {
        state.rolls = ROLL_LIST.map((list) => ({ ...list }));
      }
      return state.rolls;
    },
  },

  // actions -> 状態を変更するaction(methodsに相当)
  actions: {
    updateRolls() {
      if (this.rolls.length == 0) {
        this.rolls = ROLL_LIST.map((list) => ({ ...list }));
      }
      const nameMaster =
        this.user.nameMaster == "" ? "ご主人様" : this.user.nameMaster;
      const nameSub = this.user.nameSub == "" ? "さき" : this.user.nameSub;
      for (const rollList of this.rolls) {
        for (const roll of rollList.rolls) {
          for (const message of roll.messages) {
            message.posture = message.postureT
              .replace(/__Sub__/g, '"' + nameSub + '"')
              .replace(/__Master__/g, "「" + nameMaster + '"');
            message.message = message.messageT
              .replace(/__Sub__/g, '"' + nameSub + '"')
              .replace(/__Master__/g, '"' + nameMaster + '"')
              .replace(/\n/g, "<br />");
          }
        }
      }
      this.rolls.splice(0, 0);
    },
    /*
      increment()
      {
          this.count++
      },
      decrement()
      {
          this.counter--
      },
    */
  },
});
