
import SideMenu from "@/components/SideMenu.vue";
import HamburgerMenu from "@/components/HamburgerMenu.vue";
import MainFooter from "@/components/MainFooter.vue";
import { onMounted, ref, reactive, defineComponent } from "vue";
import { RollList } from "types/global";
import { useStore } from "@/store";
export default defineComponent({
  components: {
    SideMenu,
    HamburgerMenu,
    MainFooter,
  },
  computed: {
    rollList(): RollList[] {
      return useStore().rollList;
    },
  },
  setup() {
    useStore().updateRolls();
    const mobileView = ref(true);
    const names = reactive({
      female: useStore().user.nameSub,
      male: useStore().user.nameMaster,
    });
    const calculateWindowWidth = () => {
      return (mobileView.value = window.innerWidth < 880);
    };
    const items = reactive([
      { isRouter: false, link: "#", title: "TOP", hierarchy: "h1" },
      // eslint-disable-next-line
      { isRouter: false, link: "#images", title: "ツイッター共有画像", hierarchy: "h2" },
      // eslint-disable-next-line
      { isRouter: false, link: "#rolls", title: "ロールプレイ", hierarchy: "h1" },
      { isRouter: false, link: "#shopping", title: "通販", hierarchy: "h1" },
    ]);
    calculateWindowWidth();
    onMounted(() => {
      window.addEventListener("resize", calculateWindowWidth);
    });

    return {
      items,
      names,
      mobileView,
    };
  },
  methods: {
    onBlur() {
      useStore().user.nameSub = this.names.female;
      useStore().user.nameMaster = this.names.male;
      useStore().updateRolls();
    },
  },
});
